module.exports = ".switch input {\n  display: none; }\n\n.slider {\n  position: absolute;\n  cursor: pointer;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background-color: #ccc;\n  -webkit-transition: .4s;\n  transition: .4s; }\n\n.switch {\n  position: relative;\n  display: inline-block;\n  width: 50px;\n  height: 25px;\n  /*top: 15px;*/\n  left: 10px; }\n\n.slider:before {\n  position: absolute;\n  content: \"\";\n  height: 18px;\n  width: 18px;\n  left: 4px;\n  bottom: 4px;\n  background-color: white;\n  -webkit-transition: .4s;\n  transition: .4s; }\n\ninput:checked + .slider {\n  background-image: -webkit-gradient(linear, left top, right top, from(#00d9bf), to(#00d977));\n  background-image: linear-gradient(to right, #00d9bf, #00d977); }\n\ninput:focus + .slider {\n  -webkit-box-shadow: 0 0 1px #00d977;\n          box-shadow: 0 0 1px #00d977; }\n\ninput:checked + .slider:before {\n  -webkit-transform: translateX(26px);\n  transform: translateX(26px); }\n\n/* Rounded sliders */\n\n.slider.round {\n  border-radius: 34px; }\n\n.slider.round:before {\n  border-radius: 50%; }\n"